import init from "../_utils/plugin-init";
import scrollTo from "@/modules/_utils/scrollTo";

class ScrollNotifer {
  constructor(element, options) {
    this.element = element;
    this.name = "scroll-notifer";

    this._defaults = {
      scrollDuration: 700,
      offsetTop: 0,
      containerSelector: "body"
    };

    this.options = {
      ...this._defaults,
      ...options
    };

    this.init();
  }

  init() {
    this.buildCache();
    this.bindEvents();
  }

  buildCache() {}

  bindEvents() {
    this.element.addEventListener("click", () => this.handleClick());
  }

  scroll(to) {
    const { scrollDuration: duration, offsetTop } = this.options;
    let options = { duration };
    const position = to - offsetTop;

    scrollTo({ to: position, options });
  }

  handleClick() {
    this.scroll(
      document.querySelector(this.options.containerSelector).offsetHeight
    );
  }
}

export default init(ScrollNotifer);
