import init from "../_utils/plugin-init";

class paymentMethods {
  constructor(element, options) {
    this.element = element;
    this.name = ".payment-methods-list";

    this._defaults = {
      showSelector: `[data-security-show]`,
      hideSelector: `[data-security-hide]`,
      securityContainerSelector: `[data-security-container]`
    };

    this.options = {
      ...this._defaults,
      ...options
    };

    this.init();
  }

  init() {
    this.buildCache();
    this.bindEvents();
  }

  buildCache() {
    this.showTrigger = this.element.querySelector(this.options.showSelector);
    this.hideTriggers = this.element.querySelectorAll(
      this.options.hideSelector
    );
    this.securityContainer = this.element.querySelector(
      this.options.securityContainerSelector
    );
  }

  bindEvents() {
    const showTrigger = this.showTrigger;
    const hideTriggers = this.hideTriggers;
    const container = this.securityContainer;

    showTrigger.addEventListener("change", () => {
      if (showTrigger.checked == true) {
        container.style.display = "block";
      }
    });

    Array.prototype.forEach.call(hideTriggers, hideTrigger => {
      hideTrigger.addEventListener("click", () => {
        if (hideTrigger.checked == true) {
          container.style.display = "none";
        }
      });
    });
  }
}

export default init(paymentMethods);
